 class=\"fas fa-plus\"></i>\n                                    </b-button>\n                                </div>\n\n                                <div>\n                                    <b-button variant=\"primary\" @click=\"addToOrder\">\n                                        <i class=\"fas fa-plus\"></i>&nbsp; Add To Order\n                                    </b-button>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport {mapMutations} from 'vuex';\n\nexport default {\n    props: ['product'],\n\n    data() {\n        return {\n            qty: 1\n        };\n    },\n\n    methods: {\n        ...mapMutations({\n            addItem: 'order/ADD_PRODUCT'\n        }),\n\n        decrementQty() {\n            if (this.qty === 1) return;\n\n            this.qty--;\n        },\n\n        addToOrder() {\n            this.addItem({\n                product: this.product,\n                qty: this.qty\n            });\n\n            this.$emit('close');\n        }\n    }\n}\n</script>\n\n<style>\n.modal {\n    display: block;\n}\n\ninput::-webkit-outer-spin-button,\ninput::-webkit-inner-spin-button {\n    -webkit-appearance: none;\n    margin: 0;\n}\n\ninput[type=number] {\n    -moz-appearance: textfield;\n    text-align: center;\n}\n</style>\n"],"sourceRoot":""}]);
